import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';
import hh from '../../common/helper.js'; 
export default {
	components: {
		uniSegmentedControl
	},
	data() {
		return {
			items: ['反馈', '投票'],
			styles: [{
					value: 'button',
					text: '按钮',
					checked: true
				},
				{
					value: 'text',
					text: '文字'
				}
			],
			colors: ['#007aff', '#4cd964', '#dd524d'],
			current: 0,
			colorIndex: 0,
			activeColor: '#4cd964',
			styleType: 'button',
			newsList: []
		}
	},
	methods: {
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index
			}
		},
		newsdetail(e) {
			console.log(e);
			var newsid = e.currentTarget.dataset.newsid;
			console.log(newsid);
			uni.navigateTo({
				url: '../info/info?newsid=' + newsid,
			});
		}
	},
	onLoad: function() {
		uni.showLoading({
			title: "加载中..."
		})
		uni.request({
			url: hh.surveyListUrl,
			method: 'GET',
			data: {},
			success: res => {
				console.log(res.data);
				this.newsList = res.data;
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		});
	},
}
